import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Card, Form, Button, Row, Col, Input, Select } from "antd";

import { setOrderInfoAction } from "../../../../redux/actions";

const Info = ({ setCheckoutStep }) => {
  const [infoForm] = Form.useForm();

  const dispatch = useDispatch();

  const { selectedCarts } = useSelector((state) => state.cartReducer);

  const handleConfirmInfo = (values) => {
    dispatch(setOrderInfoAction(values));
    setCheckoutStep(2);
  };

  const renderSelectedCarts = () => {
    return selectedCarts.map((cartItem) => {
      return (
        <Row key={cartItem.id} justify="space-between">
          <p key={cartItem.id}>
            {cartItem.product?.name} x{cartItem.quantity}
          </p>
          <p>
            {(cartItem.product?.price * cartItem.quantity).toLocaleString()}₫
          </p>
        </Row>
      );
    });
  };

  return (
    <Row gutter={16}>
      <Col span={16}>
        <Card size="small">
          <Form
            form={infoForm}
            name="infoForm"
            layout="vertical"
            onFinish={(values) => handleConfirmInfo(values)}
          >
            <Form.Item
              label="Họ và tên"
              name="fullName"
              rules={[{ required: true, message: "Required!" }]}
            >
              <Input />
            </Form.Item>

            <Row gutter={24}>
              <Col span={12}>
                <Form.Item
                  label="Email"
                  name="email"
                  rules={[{ required: true, message: "Required!" }]}
                >
                  <Input />
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  label="Số điện thoại"
                  name="phoneNumber"
                  rules={[{ required: true, message: "Required!" }]}
                >
                  <Input />
                </Form.Item>
              </Col>
            </Row>

            {/* <Row gutter={24}>
              <Col span={8}>
                <Form.Item
                  label="Tỉnh/Thành"
                  name=""
                  rules={[{ required: true, message: "Required!" }]}
                >
                  <Select>
                    <Select.Option value="Hà Nội">Hà Nội</Select.Option>
                  </Select>
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item
                  label="Quận/Huyện"
                  name=""
                  rules={[{ required: true, message: "Required!" }]}
                >
                  <Select>
                    <Select.Option value="Hà Nội">Hà Nội</Select.Option>
                  </Select>
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item
                  label="Phường/Xã"
                  name=""
                  rules={[{ required: true, message: "Required!" }]}
                >
                  <Select>
                    <Select.Option value="Hà Nội">Hà Nội</Select.Option>
                  </Select>
                </Form.Item>
              </Col>
            </Row> */}

            <Form.Item
              label="Địa chỉ"
              name="address"
              rules={[{ required: true, message: "Required!" }]}
            >
              <Input />
            </Form.Item>
            <Form.Item label="Ghi chú" name="note">
              <Input.TextArea autoSize={{ minRows: 2, maxRows: 4 }} />
            </Form.Item>
          </Form>
        </Card>
      </Col>
      <Col span={8}>
        <Card size="small" style={{ marginBottom: 16 }}>
          {renderSelectedCarts()}
          <hr />
          <Row justify="space-between">
            <p>Tạm tính</p>
            <p>
              {selectedCarts
                .reduce((total, item) => {
                  return total + item.product?.price * item.quantity;
                }, 0)
                .toLocaleString()}
              ₫
            </p>
          </Row>
          <Row justify="space-between">
            <p>Giảm giá</p>
            <p>0₫</p>
          </Row>
          <Row justify="space-between">
            <p>Tổng tiền</p>
            <p>
              {selectedCarts
                .reduce((total, item) => {
                  return total + item.product?.price * item.quantity;
                }, 0)
                .toLocaleString()}
              ₫
            </p>
          </Row>
        </Card>
        <Button
          onClick={() => setCheckoutStep(0)}
          style={{ width: "100%", marginBottom: 8 }}
        >
          Trở lại
        </Button>
        <Button
          type="primary"
          style={{ width: "100%" }}
          onClick={() => infoForm.submit()}
        >
          Tiếp tục
        </Button>
      </Col>
    </Row>
  );
};

export default Info;
